import * as yup from "yup";

const DAY_MS = 24 * 60 * 60 * 1000;

export const dateValidationSchema = yup.object().shape({
    startDate: yup
        .date()
        .required(" ")
        .max(new Date(new Date().getTime() + DAY_MS), "   ")
        .typeError(" "),
    endDate: yup
        .date()
        .required(" ")
        .min(yup.ref("startDate"), "   ")
        .typeError(" "),
});
